import React from 'react';
import { Switch, Route } from "react-router-dom";
import Header from './lib/header/header';
import Footer from './lib/footer/footer';
import IndexPage from './lib/main/index/index';
import DetailPage from './lib/main/details/details';
import { services } from './lib/main/services-data';
import './scss/style.scss';


function App() {
  let routeArr = services
    .reduce(
      (acc, elem) => {
        acc.push(
          <Route key={acc.length} exec path={`/${elem.id}`} component={DetailPage} />
        );

        return acc;
      }, []
    );

  return (
    <div className="page">
      <div className="wrap">
        <Header />
        <Switch>
          {routeArr.concat([<Route key={routeArr.length} component={IndexPage} />])}
        </Switch>
        <Footer />
      </div>
    </div>
  );
}

export default App;
