import React from 'react';
import { Link } from "react-router-dom";
import './about-us.scss';

class AboutUs extends React.Component {
    render() {
        return (
            <div className="about-us" id="about-us">
                <div className="about-us-madia" />
                <div className="about-us-info">
                    <h2 className="about-us-title">Elite Spa</h2>
                    <p className="about-us-desc">Это сеть SPA салонов в городе Москва, где мы собрали самых лучших мастеров, с большим опытом и медицинским образованием под руководством экспертов из Европы.</p>
                    <p className="about-us-desc">У нас вы получите самую качественную услугу !!!</p>
                    <p className="about-us-desc">
                        <Link
                            to={{
                                pathname: "/",
                                search: "",
                                hash: "#our-services"
                            }}
                            className="about-us-link"
                        >
                            Наши услуги
                        </Link>
                    </p>
                </div>
            </div>
        );
    }
}

export default AboutUs;