export function scrollingElement() {
    return document.scrollingElement || document.documentElement;
}

export function updateScroll(props) {
    const {
        history: {
            location: {
                hash
            }
        }
    } = props;

    if (hash) {
        let node = document.getElementById(
            hash.replace("#", "")
        );
        if (node) {
            const scrollPosition = node.getBoundingClientRect();
            scrollingElement().scrollTop = scrollPosition.top;
        }
    }
}