import React from 'react';
import ReactDOM from 'react-dom';

import { Link } from "react-router-dom";
import { withRouter } from 'react-router-dom';
import './header.scss';

const siteName = "Elite Spa";

function pageResize(evt) {
    const { drop } = this.state;

    if (!drop) return;
    
    const node = ReactDOM.findDOMNode(this._refToggleMenu);
    const check = isVisible(node);

    if (!check && drop) {
        this.setState({ drop: false });
    }


}

function isVisible(elem) {
    return !!(elem.offsetWidth || elem.offsetHeight || elem.getClientRects().length);
};

class Header extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            drop: false
        };

        this._pageResize = pageResize.bind(this);
    }

    componentDidMount() {
        window.addEventListener("resize", this._pageResize);
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this._pageResize);
    }

    renderContentLogo(href) {
        return (
            <Link
                to={href}
                className="header-logo-link"
                title={siteName}
            >
                <figure
                    className="header-logo-figure"
                >
                    <img className="header-logo-img" src="/img/logo.svg" width="40" height="40" alt={siteName} />
                    <figcaption className="header-logo-name">{siteName}</figcaption>
                </figure>
            </Link>
        );
    }

    renderDivLogo(content) {
        return <div className="header-logo">{content}</div>;
    }

    renderHLogo(content) {
        return <h1 className="header-logo">{content}</h1>;
    }

    renderSmo() {
        return (
            <div className="header-smo">
                <ul className="header-smo-ul">
                    <li className="header-smo-li">
                        <a
                            href="https://facebook.com/elite.spa.moscow/"
                            className="header-smo-a"
                            title="Мы в Facebook"
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <img
                                className="header-smo-img"
                                alt="Мы в Facebook"
                                data-type="image"
                                src="/img/facebook-icon.webp"
                            />
                        </a>
                    </li>
                    <li className="header-smo-li">
                        <a
                            href="https://www.instagram.com/elite.spa.moscow/"
                            className="header-smo-a"
                            title="Мы в Instagram"
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <img
                                className="header-smo-img"
                                alt="Мы в Instagram"
                                data-type="image"
                                src="/img/instagram-icon.webp"
                            />
                        </a>
                    </li>
                </ul>
            </div>
        );
    }

    renderNavContent() {
        const { drop } = this.state;

        return (
            <div className={drop ? "header-nav-content drop" : "header-nav-content"}>
                <ul className="header-nav-ul">
                    <li className="header-nav-li">
                        <Link
                            className="header-nav-a"
                            to={{
                                pathname: "/",
                                search: "",
                                hash: "#about-us"
                            }}
                        >
                            <span className="header-nav-name">О нас</span>
                        </Link>
                    </li>
                    <li className="header-nav-li">
                        <Link
                            className="header-nav-a"
                            to={{
                                pathname: "/",
                                search: "",
                                hash: "#our-services"
                            }}
                        >
                            <span className="header-nav-name">Наши услуги</span>
                        </Link>
                    </li>
                    <li className="header-nav-li">
                        <a
                            className="header-nav-a"
                            href="tel:+79035982072"
                        >
                            <span className="header-nav-name">+7 (903) 598-20-72</span>
                        </a>
                    </li>
                </ul>
            </div>
        );
    }

    render() {
        const {
            history: {
                location: {
                    pathname
                }
            }
        } = this.props;

        const linkProps = { pathname: "/", search: "", hash: "#header" };

        return (
            <header className="header" id="header">
                {(() => {
                    if (pathname === "/") {
                        return this.renderHLogo(
                            this.renderContentLogo(linkProps)
                        );
                    } else {
                        return this.renderDivLogo(
                            this.renderContentLogo(linkProps)
                        );
                    }
                })()}

                <nav className="header-nav">
                    {this.renderSmo()}

                    <button
                        className="header-nav-toggle"
                        onClick={() => this.setState(({ drop }) => ({ drop: !drop }))}
                        ref={node => this._refToggleMenu = node}
                    >
                        <span className="header-nav-icon">
                            <span className="header-nav-line" />
                            <span className="header-nav-line" />
                            <span className="header-nav-line" />
                        </span>
                    </button>

                    {this.renderNavContent()}
                </nav>

            </header>
        );
    }
}

export default withRouter(Header);