import React from 'react';
import { withRouter, matchPath } from 'react-router';
import { Link, Redirect } from 'react-router-dom';
import { services } from '../services-data';
import { scrollingElement } from '../helpers';
import './details.scss';



class DetailPage extends React.Component {

    constructor(props) {
        super(props);

        let test = matchPath(props.history.location.pathname, {
            path: "/:id",
            exact: true
        });

        const indexItem = services.findIndex(el => el.id === test.params.id);
        const item = indexItem > -1 ? services[indexItem] : null;
        console.log("test", test.params.id);
        console.log(indexItem, item);

        this.state = {
            item
        };
    }

    componentDidMount() {
        const { title } = this.state.item;
        document.title = `${title} - Elite Spa`;
        scrollingElement().scrollTop = 0;
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.item.id !== this.state.item.id) {
            const { title } = this.state.item;
            document.title = `${title} - Elite Spa`;
            scrollingElement().scrollTop = 0;
        }
    }

    renderTitle() {
        const { title } = this.state.item;
        return <h1 className="page-details-title">{title}</h1>;
    }

    renderMedia() {
        const { imgSrc, info } = this.state.item;

        return (
            <div className="page-details-media">
                <div
                    className="page-details-img"
                    style={{
                        backgroundImage: `url("${imgSrc}")`
                    }}
                />
                <div className="page-details-info">
                    <span
                        className="page-details-info-a"
                        title={info}
                    >
                        {info}
                    </span>
                </div>
            </div>
        );
    }

    renderNavigation() {
        return (
            <div className="page-details-navigation">
                <div className="page-details-back">
                    <Link
                        to={{
                            pathname: "/",
                            search: "",
                            hash: "#our-services"
                        }}
                        className="page-details-back-link"
                        title="Elite Spa"
                    >
                        &lt;&lt; Назад к списку услуг
                    </Link>
                </div>

                <div className="page-details-singup">
                    <a
                        className="page-details-singup-link"
                        href="tel:+79035982072"
                        title="Записаться на прием!"
                    >
                        <span
                            className="page-details-singup-link-name"
                            data-full="Записаться на прием: "
                        >+7 (903) 598-20-72</span>
                    </a>
                </div>
            </div>
        );
    }

    renderContent() {
        const { content } = this.state.item;
        return (
            <div
                {...{
                    className: "page-details-content",
                    dangerouslySetInnerHTML: {
                        __html: content
                    }
                }}
            />
        );
    }

    render() {
        const { item } = this.state;
        if (item === null) {
            return (
                <Redirect
                    to={{
                        pathname: "/",
                        search: "",
                        hash: ""
                    }}
                />
            );
        }

        return (
            <main className="main page-details">
                <div className="main-container">

                    {this.renderTitle()}

                    {this.renderMedia()}

                    {this.renderNavigation()}

                    {this.renderContent()}

                </div>
            </main>
        );
    }
}

export default withRouter(DetailPage);