import React from 'react';
import { Link } from "react-router-dom";
import { services } from '../../services-data';
import './our-services.scss';

class OurServices extends React.Component {
    renderMediaItem(item) {
        return (
            <Link
                className="our-services-grid-media"
                to={{
                    pathname: item.id,
                    search: "",
                    hash: ""
                }}
                style={{
                    backgroundImage: `url("${item.imgSrc}")`
                }}
            />
        );
    }

    renderTitleItem(item) {
        return (
            <h3 className="our-services-grid-title-h3">
                <Link
                    className="our-services-grid-title-a"
                    to={{
                        pathname: item.id,
                        search: "",
                        hash: ""
                    }}
                    title={item.title}
                >
                    {item.title}
                </Link>
            </h3>
        );
    }

    renderInfoItem(item) {
        return (
            <Link
                className="our-services-grid-info-a"
                to={{
                    pathname: item.id,
                    search: "",
                    hash: ""
                }}
                title={item.title}
            >
                {item.info}
            </Link>
        );
    }

    renderMoreItem(item) {
        return (
            <Link
                className="our-services-grid-more-a"
                to={{
                    pathname: item.id,
                    search: "",
                    hash: ""
                }}
                title={item.title}
            >
                Подробнее...
            </Link>
        );
    }

    renderItems() {
        return services.map((item, index) => {
            return (
                <div
                    key={index}
                    className="our-services-grid-item"
                >
                    <div className="our-services-grid-container">
                        {this.renderMediaItem(item)}
                        <div className="our-services-grid-title">
                            {this.renderTitleItem(item)}
                        </div>
                        <div className="our-services-grid-info">
                            {this.renderInfoItem(item)}
                        </div>
                        <p className="our-services-grid-desc">
                            {item.desc}
                        </p>
                        <div className="our-services-grid-more">
                            {this.renderMoreItem(item)}
                        </div>
                    </div>
                </div>
            );
        });
    }

    render() {
        return (
            <div
                className="our-services"
                id="our-services"
            >
                <h2 className="our-services-title">Наши услуги</h2>
                <div className="our-services-grid">
                    {this.renderItems()}
                </div>
            </div>
        );
    }
}

export default OurServices;