import React from 'react';
import { withRouter } from 'react-router';
import AboutUs from './about-us/about-us';
import OurServices from './our-services/our-services';
import { updateScroll } from '../helpers';

let cacheHistoryKey = "";

class IndexPage extends React.Component {

    componentDidMount() {
        document.title = "Elite Spa";
        cacheHistoryKey = this.props.history.location.key;
        updateScroll(this.props);
    }

    componentDidUpdate(prevProps) {
        console.log(
            "componentDidUpdate 1"
        );

        if (cacheHistoryKey !== this.props.history.location.key) {
            console.log(
                "componentDidUpdate 2",
                cacheHistoryKey,
                this.props.history.location.key
            );
            cacheHistoryKey = this.props.history.location.key;
            updateScroll(this.props);
        }
    }

    render() {
        return (
            <main className="main page-index">
                <AboutUs />
                <OurServices />
            </main>
        );
    }
}

export default withRouter(IndexPage);